<template>
  <main id="main">
    <div class="container flexBox">
      <div class="listBox flexBox">
        <div
          class="beautyBox listTent"
          data-num="23"
          :style="{backgroundImage: 'url(' + BeautyImgUrl + ')'}"
        >
          <router-link to="/beauty">
            <img src="../assets/images/list_bg.png" alt="Beauty" title="Beauty" />
          </router-link>

          <div class="boxName">
            <span class="block">Beauty</span>
          </div>
          <img :src="BeautyImg.imgUrl" alt class="none" v-for="BeautyImg in BeautyList" />
        </div>
        <div
          class="cepBeautyBox listTent"
          data-num="15"
          :style="{backgroundImage: 'url(' + ConceptualBeautyImgUrl + ')'}"
        >
          <router-link to="/conceptual_beauty">
            <img
              src="../assets/images/list_bg.png"
              alt="Conceptual Beauty"
              title="Conceptual Beauty"
            />
          </router-link>
          <div class="boxName">
            <span class="block">Conceptual Beauty</span>
          </div>
          <img
            :src="consBeautyImg.imgUrl"
            alt
            class="none"
            v-for="consBeautyImg in ConceptualBeautyList"
          />
        </div>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  data() {
    return {
      BeautyList: [],
      BeautyImgUrl: "",
      ConceptualBeautyList: [],
      ConceptualBeautyImgUrl: "",
      jsonLink: "",
      i: 0,
      x: 0,
      localPath: null
    };
  },
  methods: {
    getLocalPath() {
      if (location.href.indexOf("localhost") != -1) {
        this.localPath = "../static/";
      } else {
        this.localPath = "https://mineva.lai-chih.com/";
      }
    },
    getBeautyList() {
      this.axios
        .get(this.localPath + "json/beauty.json")
        .then(res => {
          this.BeautyList = res.data;
          // console.log(this.BeautyList[0].imgUrl)
          this.BeautyImgUrl = this.BeautyList[0].imgUrl;
          console.log("data correct");
        })
        .catch(res => {
          console.log("data error");
        });
    },
    getConceptualBeautyList() {
      this.axios
        .get(this.localPath + "json/conceptual_beauty.json")
        .then(res => {
          this.ConceptualBeautyList = res.data;
          // console.log(this.BeautyList[0].imgUrl)
          this.ConceptualBeautyImgUrl = this.ConceptualBeautyList[0].imgUrl;
          console.log("data correct cons");
        })
        .catch(res => {
          console.log("data error cons");
        });
    },
    ListImgUrl() {
      this.i++;
      if (this.i == this.BeautyList.length) {
        this.i = 0;
      }
      this.BeautyImgUrl = this.BeautyList[this.i].imgUrl;
      // console.log(this.i)
      this.x++;
      if (this.x == this.ConceptualBeautyList.length) {
        this.x = 0;
      }
      this.ConceptualBeautyImgUrl = this.ConceptualBeautyList[this.x].imgUrl;
      // console.log(this.x)
    }
  },
  mounted() {
		this.getLocalPath()
    this.getBeautyList();
    this.getConceptualBeautyList();
    setInterval(this.ListImgUrl, 5000);
  }
};
</script>

<style scoped>
/* main
------------------------------------------------------------------------------*/
main {
  position: fixed;
  left: 0;
  z-index: 2;
  background-color: #ffffff;
  width: 100%;
  height: 100%;
  min-height: (500px + 1rem);
  transition: all 0.5s ease;
  padding-bottom: 1rem;
}
main .container {
  position: relative;
  justify-content: center;
  align-items: center;
}

.listBox {
  position: relative;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.listTent {
  width: 45%;
  max-width: 450px;
  position: relative;
  box-sizing: border-box;
  border: 0.1rem double #eeeeee;
  background-color: rgba(238, 238, 238, 0.5);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: all 0.35s ease;
}
.beautyBox {
  margin-right: 5%;
}

.boxName {
  position: absolute;
  bottom: -0.5rem;
  right: -5%;
  width: 110%;
  height: 0.8em;
  background-color: rgba(234, 207, 100, 0.3);
  font-size: 1.8rem;
  font-family: "Satisfy", "Josefin Slab", "", Microsoft JhengHei,
    Arial;
}
.boxName:before {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-top: 1.6rem solid transparent;
  border-right: 9rem solid rgba(234, 207, 100, 0.3);
  border-bottom: 0rem solid transparent;
  border-left: 0rem solid transparent;
  position: absolute;
  bottom: 100%;
  right: 0;
}
.boxName span {
  position: absolute;
  bottom: -0.5em;
  right: -0.3rem;
  transform: rotate(-8deg);
}
.cepBeautyBox .boxName span {
  text-align: right;
  line-height: 1;
}
.none {
  display: none;
}

/* media
----------------------------------------------------------------------------*/
/*  */
@media screen and (max-height: 580px) {
  body main {
    position: relative;
  }
  main .container {
    min-height: 600px;
  }
}
@media screen and (max-height: 580px) and (max-width: 900px) {
  body main {
    padding-top: 1rem;
  }
}
@media screen and (max-height: 800px) and (max-width: 420px) {
  body main {
    position: relative;
  }
  main .container {
    min-height: 800px;
  }
}
@media screen and (max-width: 1080px) {
}
@media screen and (max-width: 1024px) {
}
@media screen and (max-width: 900px) {
  main {
    position: relative;
    padding-bottom: 2.5rem;
    padding-top: 2.5rem;
  }
  .listBox {
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    width: 80%;
  }
  .listTent {
    width: 100%;
  }
  .beautyBox {
    margin-right: 0;
    margin-bottom: 1.5rem;
  }
}
@media screen and (max-width: 768px) {
  .boxName {
    font-size: 1rem;
  }
  .boxName:before {
    border-right: 400px solid rgba(234, 207, 100, 0.3);
  }
}
@media screen and (max-width: 500px) {
  .boxName:before {
    border-right: 240px solid rgba(234, 207, 100, 0.3);
  }
}
@media screen and (max-width: 420px) {
  main {
    position: fixed;
    height: 100%;
    padding-top: 0;
    padding-bottom: 0;
  }
}
</style>



