<template>
  <main id="main" class="pages" :class="this.$route.path.slice(1)">
    <div class="container flexBox">
      <div class="picBox grid" v-masonry transition-duration="0.3s" item-selector=".grid-item">
        <router-link
          class="goMakeupList"
          v-if="this.$route.path=='/beauty'||this.$route.path=='/conceptual_beauty'"
          to="/makeup"
        >
          Go Back List
          <img src="../assets/images/goBackArrow.png" alt />
          <i class="icon"></i>
        </router-link>
        <div
          v-masonry-tile
          v-for="(masnoryItem , ord) in masonryList"
          class="grid-item"
          :data-dir="masnoryItem.direction"
          :data-cid="masnoryItem.cid"
          @click="LBclick(ord)"
        >
          <div>
            <img :src="masnoryItem.imgUrl" :alt="masnoryItem.alt" />
          </div>
        </div>
      </div>
    </div>
    <div class="slickBlock" :class="{'open': sbOpen}">
      <div class="slicBig">
        <slick class="slickBox" ref="slickPage" :options="slickOptions">
          <div
            class="slickImg"
            v-for="(slickI , ord) in slickList"
            :data-num="slickI.cid"
            :data-dir="slickI.direction"
          >
            <div
              class="masoImg"
              :data-cid="slickI.cid"
              :alt="slickI.alt"
              :style="{'background-image': 'url('+ slickI.imgUrl + ')'}"
            ></div>
          </div>
        </slick>
        <div class="slickBlock_closeBtn" @click="LBclose">
          <span></span>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import Vue from "vue";
import { VueMasonryPlugin } from "vue-masonry";
import Slick from "vue-slick";

Vue.use(VueMasonryPlugin);

export default {
  data() {
    return {
      sbOpen: false,
      masonryList: [],
      slickList: [],
      slickOptions: {
        speed: 500,
        dots: false,
        pauseOnFocus: false,
        itemSelector: ".grid-item",
        initialSlide: "0"
      },
      jsonLink: ""
    };
  },
  components: {
    slick: Slick
  },
  beforeUpdate() {
    if (this.$refs.slickPage) {
      this.$refs.slickPage.destroy();
    }
  },
  updated() {
    if (
      this.$refs.slickPage &&
      !this.$refs.slickPage.$el.classList.contains("slick-initialized")
    ) {
      this.$refs.slickPage.create();
    }
  },
  methods: {
    changePath() {
			let _path = this.$route.path;
			let localPath
			if(location.href.indexOf('localhost') != -1){
				localPath = '../static/'

			}else{
				localPath = 'https://mineva.lai-chih.com/'

			}
      switch (_path) {
        case "/editorial":
          this.jsonLink= localPath + 'json/editorial.json'
          break;
        case "/advertising":
          this.jsonLink= localPath + 'json/advertising.json'
          break;
        case "/mens_grooming":
          this.jsonLink= localPath + 'json/mens_grooming.json'
          break;
        case "/wedding":
          this.jsonLink= localPath + 'json/wedding.json'
          break;
        case "/beauty":
          this.jsonLink= localPath + 'json/beauty.json'
          break;
        case "/conceptual_beauty":
          this.jsonLink= localPath + 'json/conceptual_beauty.json'
          break;
      }
    },
    getMasonryList() {
      this.changePath();
      this.axios
        .get(this.jsonLink)
        .then(res => {
          this.masonryList = res.data;
          this.slickList = res.data;
          // console.log(this.jsonLink);
          console.log("data correct");
        })
        .catch(res => {
          console.log("data error");
        });
    },
    LBclick(ord) {
      // console.log(this.slickOptions.initialSlide)
      this.slickOptions.initialSlide = ord;
      // console.log(this.slickOptions.initialSlide)
      this.$refs.slickPage.reSlick();
      this.sbOpen = !this.sbOpen;
      $(".toggleBtn").css({ "pointer-events": "none" });
    },
    LBclose() {
      this.sbOpen = !this.sbOpen;
      $(".toggleBtn").css({ "pointer-events": "" });
    }
  },
  mounted() {
    this.getMasonryList();
  }
};
</script>

<style scoped>
/* main
------------------------------------------------------------------------------*/
.pages {
  position: relative;
  left: 0;
  z-index: 2;
  background-color: #ffffff;
  width: 100%;
  height: 100%;
  min-height: calc(500px + 1rem);
  transition: all 0.5s ease;
  padding-bottom: 1rem;
  box-sizing: border-box;
}
/* #main.advertising{ min-height: calc(900px + 1rem);} */
#main .container {
  position: relative;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  min-height: 100vh;
}
.picBox {
  width: 80%;
  max-width: 1200px;
  margin-top: 2rem;
}

.grid-item {
  width: calc(33.3333% - 6px);
  margin: 3px;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  z-index: 0;
}
.grid-item:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.3);
  opacity: 0;
  z-index: 1;
  -webkit-transition: all 0.35s ease;
  transition: all 0.35s ease;
}
.grid-item:hover:before {
  opacity: 1;
}
.grid-item img {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.grid-item:hover img {
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
}
.goMakeupList {
  position: absolute;
  bottom: 100%;
  right: 0;
  font-size: 0.6rem;
  color: #000000;
  opacity: 0.6;
  transition: 0.3s;
}
.goMakeupList:hover {
  opacity: 1;
}
.goMakeupList img {
  width: 1.7em;
  display: inline-block;
  padding-left: 1em;
  padding-bottom: 0.2em;
  vertical-align: middle;
  opacity: 0.5;
}

/* slickBox
----------------------------------------------------------------------*/
.slickBlock {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: -1;
  opacity: 0;
  transition: all 0s ease;
  pointer-events: none;
}
.slickBlock.open {
  z-index: 99999;
  opacity: 1;
  transition: all 0.3s ease;
  pointer-events: inherit;
}
.slicBig {
  width: 80%;
  height: 80%;
  position: absolute;
  top: 10%;
  left: 10%;
}
.pages .slickBox {
  width: 80%;
  height: 80%;
  position: fixed;
  top: 10%;
  left: 10%;
}
.slickBox .slickImg {
  width: 100%;
  height: 80vh;
  position: relative;
}
.slickBox .slickImg .masoImg {
  top: 0;
  left: 0;
  width: 100%;
  height: 100% !important;
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.slickBox .slickImg img {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  position: relative;
}

/* slickBlock_closeBtn */
.slickBlock_closeBtn {
  position: absolute;
  top: -0.5rem;
  right: -0.4rem;
  width: 1rem;
  height: 1rem;
  background-color: #ffffff;
  border-radius: 50%;
  border: 4px solid #3f3f3f;
  cursor: pointer;
  transform: rotate(45deg);
}
.slickBlock_closeBtn span {
  display: block;
  width: 80%;
  height: 2px;
  background-color: #000000;
  position: absolute;
  top: 49%;
  left: 10%;
}
.slickBlock_closeBtn span:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: #000000;
  transform: rotate(90deg);
}

@media screen and (max-width: 980px) {
  .grid-item {
    width: calc(50% - 6px);
  }
}
@media screen and (max-width: 768px) {
  .slickBlock_closeBtn {
    top: -0.3rem;
    right: -0.2rem;
    width: 0.7rem;
    height: 0.7rem;
  }
  .goMakeupList {
    font-size: 0.4rem;
  }
}
@media screen and (max-width: 585px) {
  .picBox {
    width: 84%;
  }
  .grid-item {
    width: 100%;
    margin: 0 0 6px 0;
  }
}
</style>



